input {
    width: 500px;
    border: 0px;
    border-bottom: cadetblue solid 2px;
    border-left: cadetblue solid 2px;
    outline: none;
    padding: 5px;
}

form{
    grid-column: 2/3;
}

h2{
    text-align: center;
}



.logWrapper{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}


.nav{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 20px;
    
}
.nav > div:nth-child(1) {
    font-size: 30px;
}

.mainLog{
    display: grid;
    grid-column: 3/4;
    grid-template-columns: 1fr 1fr;
    justify-self: right;
    align-items: center;
}

.logs {
    display: grid;
    grid-template-columns: 1fr 1fr;
   justify-self: right;
   grid-column: 3/4;
   align-items: center;
}

.dashNav{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-self: center;
    align-items: center;
    text-align: center;
    grid-gap: 2em;
}

#login, #register{
    text-decoration: none;
    color: grey;
}
#login:hover, #register:hover{
    color: black
}


body{
    padding: 0px;
    margin: 0px;
    height: 100%;
    font-family: "arial";
    /*background-image: url("../Images/web-4841858_1280.jpg");*/
    background-attachment: fixed;
}

.PageCont{
    margin-left: 15%;
    margin-right: 15%;
    margin-top: 30px;
    background: white;
    box-shadow: 0px 0px 300px 19px rgba(0,0,0,1);
    padding: 20px;
}

.bg {
    grid-column: 1/4;
    grid-row: 1;
    height: 540px auto;
    background-image: url("https://madak.com/wp-content/uploads/2018/07/markus-spiske-518966-unsplash.jpg");
    background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.mainText{
    grid-row: 1;
    grid-column: 2/3;
    color: white;
    background-color:rgba(0, 0, 0, 0.8);
    border-radius: 100px;
    padding: 40px;
    margin: 50px;
    margin-top: 100px;
    margin-bottom: 100px;

}

.mainText > h4 {
    font-size: 30px;
}
.mainText > h5 {
    font-size: 20px;
}

.profileWrapper {
    grid-column: 2/3;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

}
.strong{
    font-weight: bold;
}

#cXi{
    grid-row: 4;
}

#QP{
    grid-row: 4;
}
#QF{
    grid-row: 4;
}

.profileWrapper > h3 {
    grid-column: 1/4;
    margin: 0px;
    padding: 10px;
    background: black;
    color: white;
    margin-bottom: 20px;
}
.profileWrapper >h5 {
    grid-row: 2;
    grid-column: 1/4;
}

.profileWrapper > button {
    grid-column: 1/4;
}

.profileWrapper >div {
    grid-row: 3;
    text-align: center;
    background: #e8e8e8;
    border: 2px solid white;
}


#yourQuizzes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1em;
}

#yourQuizzes > h5 {
    grid-column: 1/4;
}

#yourQuizzes > div {
    display: grid;
    grid-template-columns: 1fr;
}

#yourQuizzes > div > button {
    padding: 20px;
    background: white;
    border: solid 2px #bebebe;
    color: #bebebe;
    font-size: 20px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

#yourQuizzes > div > button:hover {
    background: #bebebe;
    color: white;
}

#yourQuizzes > div > button:nth-child(2):hover{
    background: red;
    color: white;
}

#nosave{
 margin-bottom: 20px;
}



.myHistory{
    margin-bottom: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    text-align: center;
}

.myHistory >div{
    border: solid #bebebe 1px
}


.dateHis{
    grid-column: 1/5;
}
.quizWrapper{
    grid-column: 2/3;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 2em;
}

.quizWrapper > h3 {
    grid-column: 1/5;
    background: black;
    color: white;
    margin: 0px;
    padding: 10px;
}

.lenImg{
    width: 100%;
}

.quizWrapper > div {
    display: grid;
    grid-template-columns: 1fr;
}

.quizWrapper > div > button {
    padding: 20px;
    background: white;
    border: solid 2px #bebebe;
    color: #bebebe;
    font-size: 20px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}


.quizWrapper > div > button:hover{
    background: #bebebe;
    color: white;
}

#quizToaddJv, #quizToaddPh, #quizToaddCp , #quizToaddRb{
    border: 0px;
    padding: 5px;
    border-bottom: 2px solid #bebebe;
    border-left: 2px solid #bebebe;
    border-right: 2px solid #bebebe;
}

#quizToaddJv:hover, #quizToaddPh:hover, #quizToaddCp:hover , #quizToaddRb:hover{
    background: green;
}

.doingQuiz{
    grid-column: 2/3;
}

.doingQuiz > button {
    width: 100%;
    padding: 20px;
    background: #bebebe;
    border: 2px solid #bebebe;
}

.doingQuiz > button:hover {
    background: green;
    color: white;
    border: 2px solid green;
}



.question{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 30px;
    grid-gap: 1em;
}

.question > p {
    grid-column: 1/3;
    margin: 0px;
}

.question > button {
    padding: 10px;
    background: white;
    border: solid 2px #bebebe;
}



.question > button:hover{
    border: solid 2px green;
}

.resultsCont {
    display: grid;
    grid-column: 2/3;
    grid-template-columns: 1fr 1fr;
    
}

.socialWrapper {
    grid-column: 2/3;
}

.personFilter {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-self: center;
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
}
.personFiltername{
    grid-column: 1/4;
    text-align: left;
    font-weight: bold;
}

#finishQuiz{
    padding: 20px;
    background: #bebebe;
    border: 2px solid #bebebe;
}

#finishQuiz:hover{
    background: green;
    color: white;
    border: 2px solid green;
}

.socialWrapper > h3 {
    background: black;
    margin: 0px;
    padding: 10px;
    color: white;
    margin-bottom: 20px;
}
.titlesFilter{
    border: solid #bebebe 1px;
}

#selectFilter {
    grid-column: 1/4;
}
#filterMessage{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    margin-top: 50px;
    margin-bottom: 50px;
}

.resultsCont > button {
    grid-column: 1/3;
}

.contForRes{
    justify-self: center;
    padding: 25px;
}

.titleQuiz{
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: black;
    padding: 10px;
    color: white;
    margin-bottom: 20px;
}

.titleQuiz > h5 {
    align-self: center;
    justify-self: right;

}

.titleQuiz > h1 {
    margin: 0px;
}

header,
main,
footer {
    padding-left: 300px;
}

@media only screen and (max-width : 992px) {

    header,
    main,
    footer {
        padding-left: 0;
    }
}
